@font-face {
  font-family: "Roboto";
  font-weight: 400;
  font-display: swap;
  src:
    url("../fonts/Roboto-Regular.ttf") format("woff");
}

body {
  width: 100%;
  margin: 0 auto;
  padding: 0;

  font-size: 16px;
  line-height: 18px;
  font-family: "Arial", "Helvetica", sans-serif;
  color: #000000;
  font-style: normal;
  font-weight: 400;
  background-color: #c1c1c1;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
